import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { SharedService } from '../../../../../services/shared.service';

@Injectable({
  providedIn: 'root',
})
export class PettyCashService {
  apiUrl = this.sharedService.getUrl;
  constructor(private http: HttpClient, public sharedService: SharedService) { }

  GetExpenses(
    searchText: string,
    sort: number,
    pageNo: number
  ): Observable<any[]> {
    return this.http.get<any[]>(
      this.apiUrl +
      `Expense/GetExpenses?Search=${searchText}&Sort=${sort}&PageNo=${pageNo}&PageSize=6`,
      this.sharedService.getHeaders()
    );
  }
  getRegisterPettyCashSituation(registerId) {
    return this.http.get(
      this.apiUrl + `PettyCash/GetRegisterPettyCashSituation?registerId=${registerId}`,
      this.sharedService.getHeaders()
    );
  }
  getApprovedExpensesSinceReplinshment(params) {
    return this.http.get(
      this.apiUrl + `PettyCash/GetApprovedExpensesSinceReplinshment${params}`,
      this.sharedService.getHeaders()
    );
  }
  getPettyCashRequestExpensesList(params) {
    return this.http.get(
      this.apiUrl + `PettyCash/GetPettyCashRequestExpensesList${params}`,
      this.sharedService.getHeaders()
    );
  }
  getExpensesDashboard(params) {
    return this.http.get(
      this.apiUrl + `PettyCash/GetExpensesDashboard${params}`,
      this.sharedService.getHeaders()
    );
  }
  getPettyCashGraphDashboard(params) {
    return this.http.get(
      this.apiUrl + `PettyCash/GetPettyCashGraphDashboard?${params}`,
      this.sharedService.getHeaders()
    );
  }
  getExpensesDashboardCards(registerId) {
    return this.http.get(
      this.apiUrl + `PettyCash/GetExpensesDashboardCards?RegisterId=${registerId}`,
      this.sharedService.getHeaders()
    );
  }

  AddExpense(expense: any): Observable<any> {
    return this.http.post<any>(this.apiUrl + 'Expense/AddExpense', expense, {
      reportProgress: true,
      observe: 'events',
      headers: new HttpHeaders({
        Authorization: 'Bearer ' + this.sharedService.getToken,
      }),
    });
  }
  confirmReceivePettyCashAmount(formData): Observable<any> {
    return this.http.post<any>(
      this.apiUrl + 'PettyCash/ConfirmReceivePettyCashAmount',
      formData,
      this.sharedService.getHeaders()
    );
  }
  uploadConfirmReceivePettyCashAmount(formData): Observable<any> {
    return this.http.post<any>(
      this.apiUrl + 'PettyCash/UploadConfirmReceivePettyCashAmount',
      formData,
      this.sharedService.getHeaders()
    );
  }
  EditExpense(expense: any): Observable<any> {
    return this.http.post<any>(this.apiUrl + 'Expense/EditExpense', expense, {
      reportProgress: true,
      observe: 'events',
      headers: new HttpHeaders({
        Authorization: 'Bearer ' + this.sharedService.getToken,
      }),
    });
  }

  GetExpenseDetails(id: number) {
    return this.http.get(
      this.apiUrl + `Expense/GetExpenseDetails?expenseRecordId=${id}`,
      this.sharedService.getHeaders()
    );
  }

  getLookupsById(id: any) {
    return this.http.get(
      this.apiUrl + `Lookups/GetLookups?lookupTypeId=${id}&pageNo=0&pageSize=1000`,
      this.sharedService.getHeaders()
    );
  }
  getExpenseCategories() {
    return this.http.get(
      this.apiUrl + `PettyCash/GetExpensesCategories?status=2001&Sort=0&PageNo=0&PageSize=1000`,
      this.sharedService.getHeaders()
    );
  }
  getPettyCashStatus() {
    return this.http.get(
      this.apiUrl + `Lookups/GetLookups?lookupTypeId=2&parentId=1`,
      this.sharedService.getHeaders()
    );
  }
  getCostCenters() {
    return this.http.get(
      this.apiUrl + `PettyCash/GetCostCenters?StatusId=2001&pageNo=0&pageSize=1000`,
      this.sharedService.getHeaders()
    );
  }
  getCostCentersByTypeId(typeId) {
    return this.http.get(
      this.apiUrl + `PettyCash/GetCostCenters?StatusId=2001&CostCenterType=${typeId}&PageSize=1000`,
      this.sharedService.getHeaders()
    );
  }
  getRegistersList() {
    let url = `Register/GetRegisters?Status=2001&PageNo=0&PageSize=100`;
    return this.http.get(
      this.apiUrl + url,
      this.sharedService.getHeaders()
    );
  }

  //==============================================
  getPettyCashExpensesNew(params): Observable<any[]> {
    return this.http.get<any[]>(
      this.apiUrl +
      `PettyCash/GetExpenses${params}`,
      this.sharedService.getHeaders()
    );
  }
  GetPettyCashExpenses(
    searchText: string,
    expenseId: string,
    sort: number,
    status: string,
    pageNo: number
  ): Observable<any[]> {
    return this.http.get<any[]>(
      this.apiUrl +
      `PettyCash/GetExpenses?Search=${searchText}&expenseId=${expenseId}&Status=${status}&Sort=${sort}&PageNo=${pageNo}&PageSize=6&RegisterId=${this.sharedService.getRegister?.registerObj?.id}`,
      this.sharedService.getHeaders()
    );
  }

  AddPettyCashExpense(expense: any): Observable<any> {
    return this.http.post<any>(this.apiUrl + 'PettyCash/AddExpense', expense, {
      reportProgress: true,
      observe: 'events',
      headers: new HttpHeaders({
        Authorization: 'Bearer ' + this.sharedService.getToken,
      }),
    });
  }

  EditPettyCashExpense(expense: any): Observable<any> {
    return this.http.post<any>(this.apiUrl + 'PettyCash/EditExpense', expense, {
      //reportProgress: true,
      //observe: 'events',
      headers: new HttpHeaders({
        Authorization: 'Bearer ' + this.sharedService.getToken,
      }),
    });
  }
  expenseRevised(expense: any): Observable<any> {
    return this.http.post<any>(this.apiUrl + 'PettyCash/ExpenseRevised', expense, {
      headers: new HttpHeaders({
        Authorization: 'Bearer ' + this.sharedService.getToken,
      }),
    });
  }
  GetPettyCashExpenseDetails(id: number) {
    return this.http.get(
      this.apiUrl + `PettyCash/GetExpenseDetails?expenseRecordId=${id}`,
      this.sharedService.getHeaders()
    );
  }

  //===============================

  GetRegisterPettyCashRequestLast(): Observable<any[]> {
    return this.http.get<any[]>(
      this.apiUrl +
      `PettyCash/GetRegisterPettyCashRequestLast?RegisterId=${this.sharedService.getRegister?.registerObj?.id}`,
      this.sharedService.getHeaders()
    );
  }

  GetRegisterPettycashrequest(
    searchText: string,
    sort: number,
    status: string,
    pageNo: number
  ): Observable<any[]> {
    return this.http.get<any[]>(
      this.apiUrl +
      `PettyCash/GetRegisterPettycashrequest?Search=${searchText}&Status=${status}&Sort=${sort}&PageNo=${pageNo}&PageSize=6&RegisterId=${this.sharedService.getRegister?.registerObj?.id}`,
      this.sharedService.getHeaders()
    );
  }
  getRegisterPettycashrequestNew(params): Observable<any[]> {
    return this.http.get<any[]>(
      this.apiUrl +
      `PettyCash/GetRegisterPettycashrequest${params}`,
      this.sharedService.getHeaders()
    );
  }

  GetRegisterPettycashrequestDetails(id: number) {
    return this.http.get(
      this.apiUrl +
      `PettyCash/GetRegisterPettycashrequestDetails?pettyCashRequestId=${id}`,
      this.sharedService.getHeaders()
    );
  }

  GetRegistersPettyCashLimits(id: number) {
    return this.http.get(
      this.apiUrl + `PettyCash/GetRegistersPettyCashLimits?Search=${id}`,
      this.sharedService.getHeaders()
    );
  }

  AddPettyCashRequest(expense: any): Observable<any> {
    return this.http.post<any>(
      this.apiUrl + 'PettyCash/AddPettyCashRequest',
      expense,
      this.sharedService.getHeaders()
    );
  }
  givePettyCashPayment(formData: any): Observable<any> {
    return this.http.post<any>(
      this.apiUrl + 'PettyCash/GivePettyCashPayment',
      formData,
      this.sharedService.getHeaders()
    );
  }
  addExpenseV2(formData: any): Observable<any> {
    return this.http.post<any>(
      this.apiUrl + 'PettyCash/AddExpenseV2',
      formData,
      this.sharedService.getHeaders()
    );
  }
  cancelGivenPettyCashPayment(formData: any): Observable<any> {
    return this.http.post<any>(
      this.apiUrl + 'PettyCash/CancelGivenPettyCashPayment',
      formData,
      this.sharedService.getHeaders()
    );
  }
  getEmployees(): Observable<any> {
    return this.http.get<any>(
      this.apiUrl + `User/GetUsers?status=2001&pageNo=0&pageSize=1000`,
      this.sharedService.getHeaders()
    );
  }
  getCashierEmployees(): Observable<any> {
    return this.http.get<any>(
      this.apiUrl + `User/GetUsers?roleId=1&status=2001&pageNo=0&pageSize=1000`,
      this.sharedService.getHeaders()
    );
  }
  getPettyCashPayments(parmas): Observable<any> {
    return this.http.get<any>(
      this.apiUrl + `PettyCash/GetPettyCashPayments${parmas}`,
      this.sharedService.getHeaders()
    );
  }

}
