import { Component, ElementRef, ViewChild } from '@angular/core';
import * as _ from 'underscore';
import { FormBuilder, FormGroup } from '@angular/forms';
import { DatePipe } from '@angular/common';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { HeaderService } from '../../../../../services/header.service';
import { PettyCashService } from '../services/petty-cash.service';
import { SharedService } from '../../../../../services/shared.service';
import { Chart } from 'chart.js';
import DatalabelsPlugin from 'chartjs-plugin-datalabels';
import { ChartConfiguration, ChartData, ChartEvent, ChartType } from 'chart.js';
import { BaseChartDirective } from 'ng2-charts';
@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent {
  searchText: string = '';
  filterParams: string = undefined;
  sort: number = 1;
  totalRecords: number = 1;
  expenseTotalAmount: number = 0;
  pageNo: number = 1;
  pageSize: number = 6;
  pettyCashExpensesList: any[] = [];
  statusList: any[] = [];
  categoriesList = [];
  registerList = [];
  filterForm: FormGroup;
  expensesCardsData;
  info;
  expensesAmount = [];
  expenseCategories = null;
  expenseCategoriesList = [];
  pieChartColors = [];
  timeInterVal = null;
  myChart: Chart;
  #region
  @ViewChild(BaseChartDirective) chart: BaseChartDirective | undefined;
  public pieChartOptions: ChartConfiguration['options'] = {
    responsive: true,
    plugins: {
      legend: {
        display: true,
        position: 'top',
        labels: {
          color: '#2D3047',
        }
      },
      datalabels: {
        formatter: (value: any, ctx: any) => {
          if (ctx.chart.data.labels) {
            //return ctx.chart.data.labels[ctx.dataIndex];
            return ctx.chart.data.datasets[0].data[ctx.dataIndex];
          }
        },
        color: '#fff',
      },
    },
  };
  pieChartPlugins = [DatalabelsPlugin];
  pieChartData;
  #endRegion
  @ViewChild('success') success: ElementRef;
  @ViewChild('barChartCanvas') expenseChartCanvas: ElementRef;
  @ViewChild('expenseCategoryCanvas') expenseCategoryCanvas: ElementRef;
  constructor(
    private _sharedService: SharedService,
    private _headerService: HeaderService,
    private _pettyCashService: PettyCashService,
    private fb: FormBuilder, public datepipe: DatePipe, private modalService: NgbModal) {
  }
  initFilterForm() {
    this.filterForm = this.fb.group({
      categoryId: [null],
      status: [null],
      registerId: [null],
      date: [''],
    })
  }
  ngOnInit() {
    this._headerService.setTitle('Petty Cash Dashboard');
    this.initFilterForm();
    this.getPettyCashStatus();
    this.getRegisters();
    this.getPettyCashCategories();
    this.filterForm.valueChanges.subscribe(value => {
      this.pageNo = 1;
      let dateObj = { fromDate: '', toDate: '' };
      let formValues;
      if (value['date']) {
        dateObj['dateFrom'] = this.datepipe.transform(value['date'][0], 'yyyy-MM-dd');
        dateObj['dateTo'] = this.datepipe.transform(value['date'][1], 'yyyy-MM-dd');;
      }
      formValues = { ...value, ...dateObj };
      delete formValues['date'];
      if (formValues.categoryId == null) {
        formValues = { ...formValues, categoryId: '' }
      }
      if (formValues.status == null) {
        formValues = { ...formValues, status: '' }
      }
      if (formValues.registerId == null) {
        formValues = { ...formValues, registerId: '' }
      }
      this.filterParams = '?' + new URLSearchParams(formValues).toString();
      if (this.myChart?.canvas) {
        this.myChart.destroy();
      }
      this.expensesAmount = [];
      this.getExpensesDashboard(this.filterParams)
    })
    //this.getExpensesDashboardCards();
  }
  ngAfterViewInit() {
    this.getExpensesDashboard(this.filterParams);
    this.getGraphData();
  }
  generateChart() {
    //const ctx = document.getElementById('expenseChart') as HTMLCanvasElement;
    const ctx = (this.expenseChartCanvas.nativeElement) as HTMLCanvasElement;
    const chartStatus = Chart.getChart(ctx.id);
    if (chartStatus) {
      chartStatus.destroy();
    }
    console.log({ ctx, chartStatus });
    this.myChart = new Chart(ctx, {
      type: 'bar',
      data: {
        labels: ['Pending Expense', 'Approved Expense', 'Rejected Expense'],
        datasets: [{
          label: 'Expenses',
          data: this.expensesAmount,
          backgroundColor: '#DC3545',
          barPercentage: 0.1,
          categoryPercentage: 0.8,
          borderSkipped: false,
          borderRadius: Number.MAX_VALUE,
        }]
      },
      options: {
        responsive: true,
        maintainAspectRatio: false,
      }
    });
  }
  getGraphData() {
    let dateObj = { fromDate: '', toDate: '' };
    if (this.timeInterVal?.length) {
      dateObj['fromDate'] = this.datepipe.transform(this.timeInterVal[0], 'yyyy-MM-dd');
      dateObj['toDate'] = this.datepipe.transform(this.timeInterVal[1], 'yyyy-MM-dd');;
    }
    else {
      const currentYear = new Date().getFullYear();
      const firstDayOfYear = new Date(currentYear, 0, 1);
      const lastDayOfYear = new Date(currentYear, 11, 31);
      dateObj['fromDate'] = this.datepipe.transform(firstDayOfYear, 'yyyy-MM-dd');
      dateObj['toDate'] = this.datepipe.transform(lastDayOfYear, 'yyyy-MM-dd');;
    }
    let params = `categoryId=${this.expenseCategories?.toString() || ''}&dateFrom=${dateObj?.fromDate}&dateTo=${dateObj?.toDate}`;
    this._pettyCashService.getPettyCashGraphDashboard(params).subscribe((response: any) => {
      if (response.isSuccess) {
        this.expenseCategoriesList = response.data;
        this.pieChartColors = [];
        this.expenseCategoriesList.forEach((x, index) => {
          if (index == this.expenseCategoriesList.length - 1)
            this.pieChartColors.unshift('rgb(220, 53, 69)');
          else
            this.pieChartColors = [...this.pieChartColors, this.getRandomGrayColor()];
        })
        this.expenseCategoriesChart();
      }
    })
  }
  handleDateChange(event) {
    this.timeInterVal = event;
    this.getGraphData()
  }
  handleCategoryChange(event) {
    this.getGraphData()
  }
  expenseCategoriesChart() {
    //const ctx = document.getElementById('pieChart') as HTMLCanvasElement;
    this.pieChartData = {
      labels: [...this.expenseCategoriesList.map(x => x.category[0].lookupName)],
      datasets: [{
        data: [...this.expenseCategoriesList.map(x => x.amount)],
        backgroundColor: [
          ...this.pieChartColors
        ],
      }]
      //labels: [['Download', 'Sales'], ['In', 'Store', 'Sales'], 'Mail Sales'],
      //datasets: [
      //  {
      //    data: [300, 500, 100],
      //  },
      //],
    };
  }
  getRandomGrayColor() {
    const grayValue = Math.floor(Math.random() * 256);
    const grayColor = `rgb(${grayValue}, ${grayValue}, ${grayValue})`;
    return grayColor;
  }
  resetDate(control) {
    if (control == 'interval') {
      this.timeInterVal = null;
    }
    else {
      this.filterForm.controls['date'].setValue('');
    }
  }
  getExpensesDashboard(filterParams = this.filterParams) {
    let defaultParams = `pageNo=${this.pageNo - 1}&pageSize=${this.pageSize}`
    this._pettyCashService.getExpensesDashboard((filterParams && filterParams + `&${defaultParams}`) || (`?${defaultParams}`)).subscribe((response: any) => {
      this.expenseTotalAmount = response?.info?.expenseTotalAmount;
      this.pettyCashExpensesList = response?.data;
      this.info = response?.info;
      this.totalRecords = response?.totalRecordCount;
      this.expensesAmount = [...this.expensesAmount, response?.info?.pending, response?.info?.approved, response?.info?.rejected];
      //this.generateChart();
    })
  }
  getExpensesDashboardCards() {
    this._pettyCashService.getExpensesDashboardCards(this._sharedService.getRegister.registerObj.id).subscribe((response: any) => {
      this.expensesCardsData = response.data;
    })
  }
  getPettyCashStatus() {
    this._pettyCashService.getPettyCashStatus().subscribe((response: any) => {
      this.statusList = response.data;
    })
  }
  getPettyCashCategories() {
    this._pettyCashService.getLookupsById(12).subscribe((response: any) => {
      this.categoriesList = response.data.map(x => {
        return {
          id: x.id,
          name: x.name[0].lookupName
        }
      });
    })
  }
  getRegisters() {
    this._pettyCashService.getRegistersList().subscribe((response: any) => {
      this.registerList = response.data;
    })
  }
  pageChange(page: number) {
    this.pageNo = page;
    this.getExpensesDashboard();
  }
}

